.side-drawer{
    height: 100%;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    position: fixed;
    background-color: #23272a;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);
}

.side-drawer img{
    max-width: 90%;
    position: fixed;
}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li{
    margin: 0.5rem 0;
}

.side-drawer a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover, .side-drawer a:active{
    color: lightgray;
    text-decoration: underline;
}

.side-drawer button{
    outline: none;
    background: transparent;
    border: none;
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
    margin: -0.4rem;
}

.side-drawer button:hover, .side-drawer button:active{
    color: lightgray;
    text-decoration: underline;
    cursor: pointer;
}

@media (min-width: 740px){
    .side-drawer{
        display: none;
    }
}
    